import React, { useEffect } from "react";
import { Card } from "react-bootstrap";
import SubscriptionPlansList from "./SubscriptionPlansList";
import { useDispatch, useSelector } from "react-redux";
import { getAllSubscriptionPlansCount } from "../../../app/features/subscriptionPlans/subscriptionPlans.slice";

const CustomCard = ({
  patientCount,
  planDuration,
  isLoading,
  isError,
  isSuccess,
}) => (
  <Card className="h-100">
    <Card.Body className="d-flex align-items-center justify-content-center flex-column">
      {isSuccess ? (
        <>
          <h3 className="fw-bold">{patientCount}</h3>
          <Card.Title className="m-0">{planDuration}</Card.Title>
        </>
      ) : isLoading ? (
        <p
          className="m-0 text-center fst-italic"
          style={{
            color: "#999999",
          }}
        >
          Loading...
        </p>
      ) : isError ? (
        <p className="m-0 text-center text-danger fst-italic">Network Error</p>
      ) : null}
    </Card.Body>
  </Card>
);

function SubscripionPlans() {
  const { subscriptionPlansCount, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.subscriptionPlans
  );

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getAllSubscriptionPlansCount());
  }, [dispatch]);

  return (
    <>
      <div className="doctor-prescription-dashbaord">
        {subscriptionPlansCount.map((data, index) => (
          <CustomCard
            key={index}
            {...data}
            isLoading={isLoading}
            isError={isError}
            isSuccess={isSuccess}
          />
        ))}
      </div>
      <div>
        <SubscriptionPlansList />
      </div>
    </>
  );
}

export default SubscripionPlans;
